.Homework {
    text-align: left;
    padding: 1rem;
    max-width: 40em;
    margin: 0 auto;
  }

 .logo {
    width: 25%;
    max-width: 500px;
}

#input {
    font-family: "Gentium Book Basic", serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 80%;
    padding: 10px;
    font-size: 24px;
    background-clip: padding-box;
    border-color: rgba(0,0,0,0.25);
    border-radius: 5px;
    line-height: 20px;
    border: 1px solid #d1d5da;
}

#button {
    color: white;
    margin-top: 10px;
    width: 80%;
    padding: 10px;
    font-size: 24px;
    border-radius: 5px;
    border: 1px solid #01638f;
    margin-bottom: 50px;
    background-color: #0184be;
    font-family: "Gentium Book Basic", serif;
}


